            text-align: center;
        }
        hr{
            background-color: #000;
        }
        .about-text{
            letter-spacing: .1rem;
            text-align: justify;
        }
        .read-about{
            margin: 10vh auto;
            text-align: center;
            letter-spacing: .4rem;
            font-weight: 500;
            font-size: 1rem;
            display: flex;
            justify-content: center;
            align-items: center;
            height: fit-content;
            width: fit-content;
            padding: 1.5vw 5vw;
            border: none;
            border-radius: 14px;
            /* Hover-effects */
            background: none;
            transition: color .4s linear;
            position: relative;
        }
        .read-about:hover{
            cursor: pointer;
            font-weight: 700;
            // color: #fff;
        }
        .read-about::before{
            content:"";
            position: absolute;
            left:0;
            top:0;
            width: 100%;
            height: 100%;
            // background: #000; 
            border: 1.2px solid black;
            border-radius: 14px;
            z-index: -1;
            transition: transform .5s;
            transform-origin: 0 0;
            transition-timing-function: cubic-bezier(0.5, 1.6, 0.4, 0.7);
        }
        .read-about::before{
            transform: scaleX(0);
        }
        .read-about:hover::before{
            transform: scaleX(1);
        }
    }
