    .swiper {
        width: 20vw;
        height: 70vh;
        position: relative;
        margin: 0 auto;
        // top: 50%;
      }
      
      .swiper-slide {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 18px;
        font-size: 22px;
        font-weight: bold;
        color: #fff;
      }
      
      .swiper-slide:nth-child(1n) {
        background: url('../images/about/work.jpg');
        background-attachment: fixed;
        background-repeat: no-repeat;
        position: relative;
        background-position: center;
        background-size: cover;
      }
      
